import React from 'react'
import { Header, Form } from '../components'
import  Tasks  from './tasks'
import Tags from './tags'


class App extends React.Component {

	constructor() {
		super()
	}

	render() {
		return (
      <div>
        <Header />
				<Form />
				<Tags handleAddTag={this.addTag} />
        <Tasks />
      </div>
		)
	}

}

export default App



